import React from 'react';
import { Button } from 'antd'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import * as CounterActions from '~/actions';

import logo from '~/static/react.svg';
import '~/static/Home.css';

import { NavLink, Route, Switch } from 'react-router-dom';
import Home from '~/containers/Home/container'
class About extends React.Component {
  static async getInitialProps({ req, res, match}) {
    console.log('res', res)
    console.log('store ', match)
    return {  };
  }
  render() {
    console.log('About props', this.props)
    return (
      <div className="Home">
        <div className="Home-header">
          <img src={logo} className="Home-logo" alt="logo" />
          <h2>Welcome to Razzle</h2>
        </div>
        <ul className="Home-resources">
          <li>
            <NavLink to="/">Home</NavLink>
          </li>
          <li>
            <NavLink to="/about">About</NavLink>
          </li>
          <li>
            <NavLink to="/about/test">About and id</NavLink>
          </li>
        </ul>
        <div>
          <Switch>
            <Route
              path="/about/"
              exact
              render={() => <div>Is about</div>}
            />
            <Route
              path="/about/:id"
              exact
              component={ Home }
            />
          </Switch>
          <Button onClick={ this.props.increment } type="primary">+</Button>
          { this.props.count }
          <Button onClick={ this.props.decrement } type="primary">-</Button>
        </div>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  count: state.counter.count,
});

function mapDispatchToProps(dispatch) {
  return bindActionCreators(CounterActions, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(About);



